<script>
import { Component, Vue } from 'vue-property-decorator'
import { Carousel, Slide } from 'vue-carousel';


const btnArr = document.getElementsByTagName('button');

for (let i = 0; i < btnArr.length; i++) {

  btnArr[i].addEventListener('click', function (e) {
    e.preventDefault();
    document.querySelector('.box' + (i + 1)).scrollIntoView(true);
  });

}

//향 옵션 드롭다운
window.onload = () => {
  document.querySelector('.i1_scentDropbtn_click').onclick = () => {
    i1_scentDropdown();
  }
  document.getElementsByClassName('i1_fastfood').onclick = () => {
    i1_showMenu(value);
  };


  i1_scentDropdown = () => {
    var v = document.querySelector('.i1_scentDropdown-content');
    var i1_scentDropbtn = document.querySelector('.i1_scentDropbtn')
    v.classList.toggle('show');
  }

  i1_showMenu = (value) => {
    var i1_scentDropbtn_icon = document.querySelector('.i1_scentDropbtn_icon');
    var i1_scentDropbtn_content = document.querySelector('.i1_scentDropbtn_content');
    var i1_scentDropbtn_click = document.querySelector('.i1_scentDropbtn_click');
    var i1_scentDropbtn = document.querySelector('.i1_scentDropbtn');

    i1_scentDropbtn_icon.innerText = '';
    i1_scentDropbtn_content.innerText = value;
    i1_scentDropbtn.style.borderColor = '#3992a8';
  }
}
window.onclick = (e) => {
  if (!e.target.matches('.i1_scentDropbtn_click')) {
    var i1_scentDropdowns = document.getElementsByClassName("i1_scentDropdown-content");

    var i1_scentDropbtn_icon = document.querySelector('.i1_scentDropbtn_icon');
