//쇼핑백 옵션 드롭다운
window.onload = () => {
  document.querySelector('.i1_bagDropbtn_click').onclick = () => {
    i1_bagDropdown();
  }
  document.getElementsByClassName('i1_fastfood').onclick = () => {
    i1_showMenu(value);
  };


  i1_bagDropdown = () => {
    var v = document.querySelector('.i1_bagDropdown-content');
    var i1_bagDropbtn = document.querySelector('.i1_bagDropbtn')
    v.classList.toggle('show');
  }

  i1_showMenu = (value) => {
    var i1_bagDropbtn_icon = document.querySelector('.i1_bagDropbtn_icon');
    var i1_bagDropbtn_content = document.querySelector('.i1_bagDropbtn_content');
    var i1_bagDropbtn_click = document.querySelector('.i1_bagDropbtn_click');
    var i1_bagDropbtn = document.querySelector('.i1_bagDropbtn');

    i1_bagDropbtn_icon.innerText = '';
    i1_bagDropbtn_content.innerText = value;
    i1_bagDropbtn.style.borderColor = '#3992a8';
  }
}
window.onclick = (e) => {
  if (!e.target.matches('.i1_bagDropbtn_click')) {
    var i1_bagDropdowns = document.getElementsByClassName("i1_bagDropdown-content");

    var i1_bagDropbtn_icon = document.querySelector('.i1_bagDropbtn_icon');
    var i1_bagDropbtn_content = document.querySelector('.i1_bagDropbtn_content');
    var i1_bagDropbtn_click = document.querySelector('.i1_bagDropbtn_click');
    var i1_bagDropbtn = document.querySelector('.i1_bagDropbtn');

    var i;
    for (i = 0; i < i1_bagDropdowns.length; i++) {
      var openi1_bagDropdown = i1_bagDropdowns[i];
      if (openi1_bagDropdown.classList.contains('show')) {
        openi1_bagDropdown.classList.remove('show');
      }
    }
  }
}


// 상단 이미지 호버
window.onload = function () {
  let bigPic = document.querySelector('.big');
  let smallPics = document.querySelectorAll('.small');

  for (let i = 0; i < smallPics.length; i++) {
    smallPics[i].onmouseover = changepic;
  }

  function changepic() {
    let smallPicsAttribute = this.getAttribute('src');
    bigPic.setAttribute('src', smallPicsAttribute);
  }
}

